import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            tabList: [
                {id:8, name: '下单提醒'},
                {id:4, name: '退款提醒'},
                {id:1, name: '售后提醒'}
            ],
            theme: {
                color: '#446dfd'
            },
            more_list: false,
            dialog: false,
            activeTab: 8,
            chooseIndex: [0],
            choose_list: [],
            list: [],
            newIndex: 0,
            page: 1,
            loading: false,
            today: '',
            yesterday: '',
            active: {
                sign: 'all',
                name: '全部',
            },
            num: {}
        }
    },
    components: {
        "app-tab-nav": appTabNav,
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin,
        })
    },
    methods: {
        choose() {
            this.dialog = true;
        },

        toggle(boolean,type) {
            this.dialog = boolean;
            if(type == 1) {
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                this.chooseIndex[0] = this.newIndex;
                this.active = this.choose_list[this.chooseIndex[0]];
                this.list = [];
                this.page = 1;
                if (this.activeTab == 1) {
                    this.getPut();
                } else {
                    this.getList();
                }
            }else {
                this.newIndex = this.chooseIndex[0];
            }
        },

        bindChange(e) {
            this.newIndex = e.detail.value[0];
        },
        
        toOrder(id) {
            uni.navigateTo({
                url: '/pages/app_admin/order/order?no='+id+'&msg=1'
            }); 
        },
        tabStatus(e) {
            this.list = [];
            this.page = 1;
            this.activeTab = e.currentTarget.dataset.id;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            if (this.activeTab == 1) {
                this.getPut();
            } else {
                this.getList();
            }
        },
        getPut() {
            let that = this;
            if(that.loading) {
                return false
            }
            that.loading = true;
            that.$request({
                url: that.$api.app_admin.refund,
                data: {
                    status: 0,
                    sign: that.active.sign,
                    page: that.page
                }
            }).then(response=>{
                that.loading = false;
                uni.hideLoading();
                if(response.code == 0) {
                    response.data.list.forEach(function(row,index){
                        if (row.created_at.substring(0,10) == that.today) {
                            row.created_at = row.created_at.substring(11)
                        } else if (row.created_at.substring(0, 10) == that.yesterday) {
                            row.created_at = '昨天'
                        }else {
                            row.created_at = row.created_at.substring(0, 10)
                        }
                    })
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                    that.more_list = false;
                    if (response.data.list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.loading = false;
                uni.hideLoading();
            });
        },
        getList() {
            let that = this;
            if(that.loading) {
                return false
            }
            that.loading = true;
            that.$request({
                url: that.$api.app_admin.order,
                data: {
                    status: that.activeTab,
                    plugin: that.active.sign,
                    page: that.page
                }
            }).then(response=>{
                that.loading = false;
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.choose_list = response.plugin_list;
                    response.data.list.forEach(function(row,index){
                        if (row.created_at.substring(0,10) == that.today) {
                            row.created_at = row.created_at.substring(11)
                        } else if (row.created_at.substring(0, 10) == that.yesterday) {
                            row.created_at = '昨天'
                        }else {
                            row.created_at = row.created_at.substring(0, 10)
                        }
                    })
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                    that.more_list = false;
                    if (response.data.list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.loading = false;
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    },

    onReachBottom() {
        if(this.more_list) {
            uni.showLoading({
                mask: true,
                title: '加载更多...'
            });
            if (this.activeTab == 1) {
                this.getPut();
            } else {
                this.getList();
            }
        }
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        var myDate = new Date();
        // 今天
        let year = myDate.getFullYear();
        let month = myDate.getMonth() + 1;
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        let now = myDate.getDate();
        that.today = year + "-" + month + "-" + now;
        var timestamp = Date.parse(new Date());
        // 昨天
        let yesterTime = (timestamp / 1000 - 24 * 60 * 60) * 1000;
        let yesterDate = new Date(yesterTime)
        let yester_year = yesterDate.getFullYear();
        let yester_month = yesterDate.getMonth() + 1;
        if (yester_month >= 1 && yester_month <= 9) {
            yester_month = "0" + yester_month;
        }
        let yester_now = yesterDate.getDate();
        that.yesterday = yester_year + "-" + yester_month + "-" + yester_now;
        that.getList();
    }
}
